import React from "react";
import '../../Themes/scss/ThemesSplide.scss';
import './MediaCarousel.scss';
import Image from "../../Themes/Elements/Media/Image";
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/pagination';

const MediaCarousel = ({ Post, ImageClass }) => {

       const MediaImage = (Post, ImageClass, Item, index) => (
              <SwiperSlide key={index ? `slider_${index}` : null}>
                <Image 
                  ImageClass={ImageClass} 
                  Post={Post} 
                  mediaOnly={Item || false} 
                  size={false} 
                  observer={false} 
                />
              </SwiperSlide>
            );
            

    return (
        <>
            <Swiper
                modules={[Pagination, Navigation]}
                spaceBetween={0}
                autoHeight
                slidesPerView={1}
                pagination={false}
                navigation={{
                    prevEl: `#tb_c_p_prev-${Post.id}`,
                    nextEl: `#tb_c_p_next-${Post.id}`,
                }}
                className="tb_c_p__media_slider"
            >
                {Post.mediaList.length > 0 ? (
                    Post.mediaList.map((Item, index) => (
                        <MediaImage ImageClass={ImageClass} Post={Post} Item={Item} index={index} />    
                    ))
                 ) : (
                     <MediaImage ImageClass={ImageClass} Post={Post} />   
                )}
            </Swiper>

            {Post?.mediaList.length > 1 && (
                <div className="tb_c_p__arrow_wrapper_" onClick={(e) => e.stopPropagation()}>
                    <div id={`tb_c_p_prev-${Post.id}`} className="tb_c_p__arrow tb_c_p__arrow_left__ tb__icon tb-arrow-left-alt">
                        <div></div>
                    </div>
                    <div id={`tb_c_p_next-${Post.id}`} className="tb_c_p__arrow tb_c_p__arrow_right__ tb__icon tb-arrow-right-alt">
                        <div></div>
                    </div>
                </div>
            )}
        </>
    );
};

export default React.memo(MediaCarousel);
