import React, { PureComponent } from "react";
import Masonry from 'react-masonry-component';
import Card from "./Elements/Card";
import './scss/GridTheme.scss'
class GridTheme extends PureComponent {
  state = {
    postData: [],
    loadData: 1
  }
  componentDidMount() {
    const { postData } = this.props;
    setTimeout(() =>  this.setState({ postData, loadData: 1 }, () => this.onLoadMasonry()) , 100)
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    this.setState({ postData, loadData: 1 }, () => this.onLoadMasonry())
  }
  
  render() {
    const { postData, completeDataObject, adjustWidth, CardStyle, ThemeStyle, postSize, clickToShowPopUp, onClickPopUpSlider } = this.props;
    const { loadData } = this.state;
    return (
      <Masonry
        className={`tb_g_post_container`}
        tabIndex='0'
        aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
        elementType={'div'}
        options={{
          transitionDuration: 0,
          percentPosition: true,
          itemSelector: '.tb_g_post_wrapper',
          horizontalOrder: true,
          resize: true,
        }}
        disableImagesLoaded={false}
        updateOnEachImageLoad={false}
        imagesLoadedOptions={{}}
        enableResizableChildren={true}>{
          loadData ?
            ((postData && postData.length > 0)) && postData.map((item, index) => {
              const cardData = completeDataObject[item];
              return  <Card adjustWidth={cardData.highlight == 1 ? adjustWidth * 2 : adjustWidth}  clickToShowPopUp={clickToShowPopUp} key={index} onClickPopUpSlider={onClickPopUpSlider} Post={cardData} postSize={postSize} itemIndex={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} />
            }) : null
        }
      </Masonry>
    );
  }
}

export default GridTheme