import React, { useEffect } from "react";
import AuthorDetails from "./AuthorDetails";
import MediaCarousel from "../../../CommonComponents/MediaCarousel";
import PostContent from '../../Elements/PostContent/PostContent'
import AverageRating from "../../Elements/AverageRating/AverageRating";
import { isTagShop } from '../../../../actions/api';
import { NO_PRODUCT } from "../../../../constants";
import { PostTrimContent } from '../../../../utils'
const trim_content_length = 200;
const Card = ({ Post, adjustWidth, CardStyle, clickToShowPopUp, itemIndex, ThemeStyle, ThemeInfo, onClickPopUpSlider }) => {
  const cardSize = { width: `${adjustWidth}%`, padding: CardStyle.style.padding / 2 };
  const mediaType = Post.type != 1 ? true : false;
  const postTextContent = CardStyle.content.trimcontent ? PostTrimContent(Post.content.text, trim_content_length) : Post.content.text;
  
  useEffect(() => {
    if (Post.isPopUp) onClickPopUpSlider(itemIndex, Post)
  }, [itemIndex, Post.isPopUp, Post]);
  const products = Post.products
  return (
    <div id={`tb_g_id-${Post.id}`} className={`tb_g_post_wrapper`} tb-network={Post.network.id} role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${postTextContent}`} style={cardSize} tabIndex="0">
      <div className="tb_g_post_in" onClick={clickToShowPopUp(itemIndex, Post)} role="button" aria-label="Popup" tabIndex={"0"}>
        {mediaType ? 
        <div className="tb_g_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
            <MediaCarousel ImageClass="tb_g_image" VideoClass="tb_g_video" Post={Post} CardStyle={CardStyle} ThemeInfo={ThemeInfo} ThemeStyle={ThemeStyle} />
          </div>
        : null}
        <div className="tb_g_contant_wrapper">
          <AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} mediaType={mediaType} size={40}  />
          {Post.rating > 0 ? <div className="tb_g_rating__"><AverageRating size={20} rating={Post.rating} /> </div> : ''}
          {(isTagShop && CardStyle.content.status) ?
            <PostContent contentClass={`tb_g_content`} Post={Post} content={postTextContent} CardStyle={CardStyle} contentTitle={Post.contentTitle} /> 
            : ''}
          {Post.isProduct ? 
          <div className="tb_g_p_wrp">
            <div className="tb_g_p_">
              <img className={'tb_g_p_img'} decoding="async" src={products[0]?.image} width="187" height="259" alt="" loading="lazy" draggable="false" onError={(e) => e.target.src = NO_PRODUCT} />
            </div>
            <div className="tb_g_p_info">
              <div className="tb_g_p_t">{products[0]?.title}</div>
              <div className="tb_g_p_p">{products[0]?.currency}{products[0]?.discount > 0 ? products[0]?.discount : products[0]?.price}</div>
            </div>
          </div> : null}
        </div>
      </div>
    </div >
  );
}
export default Card